import * as React from 'react';
import styled from 'styled-components';
import { useSelector } from 'hooks/useSelector';

import { CatalogListItemTouch } from '../ListItem/CatalogListItem@touch';

const StyledCatalogList = styled.div`
    display: flex;
    flex-wrap: wrap;
    padding: 0 11px;
`;

export const CatalogListTouch: React.FC = () => {
    const itemIds = useSelector(({ catalogPage }) => catalogPage.order);
    const memorizedItemsList = React.useMemo(
        () => itemIds.map((itemId) => <CatalogListItemTouch key={itemId} id={itemId} />),
        [itemIds],
    );

    return <StyledCatalogList>{memorizedItemsList}</StyledCatalogList>;
};
