import React from 'react';
import styled from 'styled-components';
import { Skeleton } from 'components/Skeleton/Skeleton';

const itemWidth = 392;
const itemHeight = 592;
const columnCount = 5;
const paddingBetween = 32;
const width = itemWidth * columnCount + paddingBetween * (columnCount - 1);

const StyledRoot = styled.div`
    width: ${width}px;
    height: ${itemHeight}px;
    padding: 120px 0 0 128px;
`;

export const CatalogSkeleton: React.FC = () => (
    <StyledRoot>
        <Skeleton
            viewBox={`0 0 ${width} ${itemHeight}`}
            width="100%"
            height="100%"
            backgroundColor="#262828"
            foregroundColor="#414545"
            itemWidth={itemWidth}
            itemHeight={itemHeight}
            borderRadius={24}
            rowCount={1}
            columnCount={columnCount}
            paddingBetweenRows={paddingBetween}
            paddingBetweenColumns={paddingBetween}
            speed={2}
        />
    </StyledRoot>
);
